import {
  Container,
  TextField,
  Button,
  Modal,
  Box,
  Typography,
  IconButton,
  Grid,
  TextareaAutosize,
  Chip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import "./css/Product.css";
import { Link, Navigate } from "react-router-dom";
import "./product.css";
const Product = () => {
  const navigate = useNavigate();
  const prod = [
    {
      id: 1,
      src: "https://media.pakfactory.com/media_upload/coding_guide/folding-carton-boxes.webp",
      Title: "Pentagone Packaging ",
      para: "Pentagone Packaging Pentagone Packaging",
    },
    {
      id: 2,
      src: "https://media.pakfactory.com/media_upload/coding_guide/folding-carton-boxes.webp",
      Title: "Pentagone Packaging ",
      para: "Pentagone Packaging Pentagone Packaging",
    },
    {
      id: 3,
      src: "https://media.pakfactory.com/media_upload/coding_guide/folding-carton-boxes.webp",
      Title: "Pentagone Packaging ",
      para: "Pentagone Packaging Pentagone Packaging",
    },
    {
      id: 4,
      src: "https://media.pakfactory.com/media_upload/coding_guide/folding-carton-boxes.webp",
      Title: "Pentagone Packaging ",
      para: "Pentagone Packaging Pentagone Packaging",
    },
  ];
  const singlcomponent = () => {
    navigate("./Productopen");
  };
  return (
    <>
      <Grid container spacing={2}>
        <Grid
          item
          xs={12}
          sm={12}
          style={{ backgroundColor: "White", marginTop: "5%" }}
        >
          <div className="d-flex justify-content-between">
            <div className="ms-5">
              <Typography
                variant="h2"
                gutterBottom
                style={{ color: "#1b80ad" }}
              >
                Products:
              </Typography>
            </div>
            <div className="seemoreHead">
              <Link to="/products">
                <h5 style={{ color: "#1b80ad", marginRight: "50px" }}>
                  Visit More{" "}
                  <i class="fa fa-arrow-circle-o-right" aria-hidden="true"></i>
                </h5>
              </Link>
            </div>
          </div>

          <p className="container ms-3" style={{ textAlign: "justify" }}>
            Pentagone: Elevate your with, a sleek and innovative [type of
            product]. Designed for [target audience], it boasts [key features,
            e.g., durability, efficiency] and adds a touch of style to your
            [setting]. Experience excellence in [industry or lifestyle] with
            Pentagone's [Product Name].
          </p>
          <div
            className="row type-cat m-5 ar7_product_box"
            id="allproduct-section"
          >
            {prod.map((product) => (
              <div
                key={product.id}
                onClick={singlcomponent}
                className="col-md-3 col-sm-6 mb-4 ar7_product_sub_box"
              >
                <div className="ar7_product_card_1" style={{ height: "420px" }}>
                  <a>
                    <img
                      src={product.src}
                      alt={product.Title}
                      style={{ height: "250px", width: "100%" }}
                    />
                    <div className="section-type-box-des">
                      <h3>{product.Title}</h3>
                      <p>{product.para}</p>
                    </div>
                  </a>
                </div>
              </div>
            ))}
          </div>
        </Grid>
      </Grid>
    </>
  );
};
export default Product;
