import React, { useState } from "react";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import { IconButton } from "@mui/material";
import "./Login.css";
import InstagramIcon from "@mui/icons-material/Instagram";
import FacebookIcon from "@mui/icons-material/Facebook";
import logo from "../../Assete/images/Logo/Pentagone tape.png";
import img2 from "../../Assete/images/slideshow/team-meeting-renewable-energy-project.jpeg";
import Navbar from "../../NavFoot/NavBar/Navbar";
import Footer from "../../NavFoot/Footer/Footer";
import { Link, useNavigate } from "react-router-dom";
import { serverURL } from "../../configs/Variables";
import axios from "axios";
import { Toast } from "bootstrap";

const Login = () => {
  let [email, setEmail] = useState("");
  let [password, setPassword] = useState("");
  let [loginProcessStatus, setLoginProcessStatus] = useState(false);
  let [logging, setLogging] = useState(false);
  let [loginSuccessfull, setLoginSuccessfull] = useState(false);
  let navigate = useNavigate();
  let handleLogin = (e) => {
    e.preventDefault();
    let forServer = { email, password };
    setLogging(true);
    axios
      .post(`${serverURL}/api/user/login`, forServer)
      .then((response) => {
        setLogging(false);
        console.log(response.data);
        setLoginProcessStatus("Login Successfull.");
        let authToken = response.data.authToken;
        localStorage.setItem("pentagon_packaging", authToken);
        setLoginSuccessfull(true);
        navigate("/");
      })
      .catch((error) => {
        setLogging(false);
        console.log(error);
        setLoginProcessStatus(error.response.data.message);
      });
  };
  return (
    <>
      <Navbar />
      <section
        className="h-100 gradient-form"
        style={{ marginBottom: "60px", marginTop: "60px" }}
      >
        <div className="container py-5 h-100">
          <div className="row d-flex justify-content-center align-items-center h-100">
            <div className="col-xl-10">
              <div className="card rounded-3 text-black">
                <div className="row g-0">
                  <div className="col-lg-6">
                    <div className="card-body p-md-5 mx-md-4">
                      <div className="text-center">
                        <img
                          src={logo}
                          style={{
                            height: "130px",
                            width: "130px",
                            marginBottom: "30px",
                          }}
                          alt="pentagone_Logo"
                        />
                        {/* <h4 className="mt-1 mb-5 pb-1">Pentagone Packaging</h4> */}
                      </div>
                      {!loginSuccessfull && (
                        <form onSubmit={handleLogin}>
                          <p>Please login to your account</p>
                          {loginProcessStatus && <p>{loginProcessStatus}</p>}
                          <div className="form-outline mb-5">
                            <input
                              type="email"
                              id="form2Example11"
                              className="form-control"
                              placeholder="Phone number or email address"
                              autoComplete="email"
                              value={email}
                              required
                              onChange={(e) => {
                                setEmail(e.target.value);
                              }}
                            />
                          </div>
                          <div className="form-outline mb-3">
                            <input
                              type="password"
                              id="form2Example22"
                              className="form-control"
                              placeholder="Password"
                              autoComplete="current-password"
                              value={password}
                              required
                              onChange={(e) => {
                                setPassword(e.target.value);
                              }}
                            />
                            {/* <label className="form-label" htmlFor="form2Example22">
                          Password
                        </label> */}
                          </div>
                          <div>
                            <a className="text-muted ">Forgot password?</a>
                          </div>
                          <div className="text-center  ">
                            <button
                              className="btn btn-primary btn-block fa-lg gradient-custom-2 borderRadius"
                              type="submit"
                              style={{
                                margin: "30px",
                                backgroundColor: "skyblue",
                              }}
                            >
                              {!logging && <>Log in</>}
                              {logging && (
                                <>
                                  Logging in{" "}
                                  <i className="fa-solid fa-spinner fa-spin" />
                                </>
                              )}
                            </button>
                          </div>
                          <div className="d-flex justify-content-center pb-4">
                            <p className="mb-0 me-2">Don't have an account?</p>
                            <Link to="/signup">
                              <p
                                style={{ color: "skyblue", cursor: "pointer" }}
                              >
                                Create new
                              </p>
                            </Link>
                          </div>
                        </form>
                      )}
                      {loginSuccessfull && (
                        <div
                          style={{
                            fontSize: "2rem",
                            backgroundColor: "green",
                            color: "white",
                            padding: "2rem",
                            borderRadius: "5px",
                          }}
                        >
                          Login Successfull{" "}
                          <i className="fa-solid fa-square-check fa-bounce" />{" "}
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="col-lg-6 d-flex align-items-center gradient-custom-2">
                    <div className="text-white px-3 py-4 p-md-5 mx-md-4">
                      <h3 className="mb-4 fw-bold">
                        We are more than just a company
                      </h3>
                      <p className="text-white">
                        Lorem ipsum dolor sit amet, consectetur adipisicing
                        elit, sed do eiusmod tempor incididunt ut labore et
                        dolore magna aliqua. Ut enim ad minim veniam, quis
                        nostrud exercitation ullamco laboris nisi ut aliquip ex
                        ea commodo consequat.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Login;
