import React, { useState } from "react";
import "./quote.css";
const Quote = () => {
  const [selectedOption, setSelectedOption] = useState("");

  const handleSelectChange = (e) => {
    setSelectedOption(e.target.value);
  };

  return (
    <>
      <div
        className="content p-4 ar7_original_form"
        style={{
          border: "1px solid transparent",
          borderRadius: "10px",
          backgroundColor: "#70d3ff",
        }}
      >
        <h3 className="justify-content-center fw-bold ms-5 mt-4">
          GET INSTANT QUOTE
        </h3>
        <div className=" justify-between text-black">
          <div class="my-1">
            <label>PRODUCT INFORMATION</label>
            <input
              type="email"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Product Name?"
            />
          </div>
          <div class="my-1 ">
            <label>upload artwork:</label>
            <input type="file" class="form-control" placeholder="Stock" />
          </div>
        </div>
        <div class="my-1">
          <label>SELECT SIZE</label>
          <div className="d-flex">
            <input
              type="number"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Lenth"
            />
            &nbsp;
            <input
              type="number"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Width"
            />
            &nbsp;
            <input
              type="number"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Depth"
            />
            &nbsp;
            <input
              type="number"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Units"
            />
            &nbsp;
          </div>
        </div>
        <div class="my-1">
          <label>CHOOSE MATERIALS:</label>
          <div className="d-flex">
            <select value={selectedOption} onChange={handleSelectChange}>
              <option value="stock">Stock</option>
              <option value="12pt">12pt</option>
              <option value="14pt">14pt</option>
              <option value="16pt">16pt</option>
              <option value="18pt">18pt</option>
              <option value="20pt">20pt</option>
              <option value="22pt">22pt</option>
              <option value="24pt">24pt</option>
              <option value="cardboard">cardboard stock</option>
              <option value="carrugated">corrugated stock</option>
              <option value="kraft">kraft stock</option>
              <option value="other">other</option>
            </select>
            <p> {selectedOption}</p>
            &nbsp;
            <select value={selectedOption} onChange={handleSelectChange}>
              <option value="color">color</option>
              <option value="1 color">1 color</option>
              <option value="2 color">2 color</option>
              <option value="3 color">3 color</option>
              <option value="4 color">4 color</option>
              <option value="4/1 color">4/1 color</option>
              <option value="4/2 color">4/2 color</option>
              <option value="4/3 color">4/3 color</option>
              <option value="4/4 color">4/4 color</option>
            </select>
            <p> {selectedOption}</p>
            &nbsp;
            <input
              type="number"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Quantity"
            />
            &nbsp;
          </div>
        </div>
        <div class="my-1">
          <label>Personal Information</label>
          <div className="d-flex">
            <input
              type="text"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Full Name"
            />
            &nbsp;
            <input
              type="email"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Email ID"
            />
            &nbsp;
            <input
              type="number"
              class="form-control"
              style={{ borderRadius: "4px" }}
              placeholder="Contact Number"
            />
            &nbsp;
          </div>
        </div>
        <div class="my-1">
          <label>Additional Information</label>
          <div className="d-flex">
            <textarea
              className="form-control"
              name=""
              id=""
              cols="30"
              rows="10"
              placeholder="Message...?"
            ></textarea>
          </div>
        </div>
        <center>
          <button
            className="btnSty"
            style={{ margin: "7px", borderRadius: "5px" }}
          >
            Submit
          </button>
        </center>
      </div>
    </>
  );
};
export default Quote;
