import React, { useState } from "react";
// import "./Detail1.css";
import "./detail1_ar7.css";
import img1 from "../../../Assete/images/Home/New folder/satisfaction.png";
import img2 from "../../../Assete/images/Home/New folder/budgeting.png";
import img3 from "../../../Assete/images/Home/New folder/efficiency.png";
import img4 from "../../../Assete/images/Home/New folder/delivery-man.png";
import img5 from "../../../Assete/images/Home/New folder/premium.png";
import img6 from "../../../Assete/images/Home/New folder/achievement.png";
const Detail1 = () => {
  const [position, setPosition] = useState(0);
  const [startX, setStartX] = useState(0);

  const det = [
    // Your data array
    {
      name: "100% Customer Satisfaction",
      img: "../../../Assete/images/slideshow/team-meeting-renewable-energy-project.jpeg",
    },
    { name: "aaa", img: "../../../Assete/images/Home/cardboard box.png" },
    { name: "aaa", img: "../../../Assete/images/Home/home.png" },
    { name: "aaa", img: "../../../Assete/images/Home/home.png" },
    { name: "aaa", img: "../../../Assete/images/Home/home.png" },
  ];

  const handleTouchStart = (e) => {
    setStartX(e.touches[0].clientX);
  };

  const handleTouchMove = (e) => {
    const deltaX = e.touches[0].clientX - startX;
    setPosition(position + deltaX);
    setStartX(e.touches[0].clientX);
  };

  const handleMove = (direction) => {
    const step = 100; // Adjust the step size for smoother scrolling

    if (direction === "left") {
      setPosition(position - step);
    } else if (direction === "right") {
      setPosition(position + step);
    }
  };

  return (
    <>
      <h1 className="text-center" style={{ color: "#1b80ad" }}>
        Our Service Type
      </h1>
      <div className="ar7_image_box">
        <img
          src={img1}
          className="ms-5 me-5 boxes"
          style={{ height: "110px", width: "110px" }}
        />
        <img
          src={img2}
          className="ms-5 me-5 boxes"
          style={{ height: "110px", width: "110px" }}
        />
        <img
          src={img3}
          className="ms-5 me-5 boxes"
          style={{ height: "110px", width: "110px" }}
        />
        <img
          src={img4}
          className="ms-5 me-5 boxes"
          style={{ height: "110px", width: "110px" }}
        />
        <img
          src={img5}
          className="ms-5 me-5 boxes"
          style={{ height: "110px", width: "110px" }}
        />
        <img
          src={img6}
          className="ms-5 me-5 boxes"
          style={{ height: "110px", width: "110px" }}
        />
      </div>
    </>
  );
};

export default Detail1;
