import "./App.css";
import ReactDOM from "react-dom/client";
import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from "./Main/Home/Home";
import Login from "./CombineComp/Login/Login";
import SignUp from "./CombineComp/SignUp/SignUp";
import ContactUs from "./Main/ContactUs/ContactUs";
import GetQuote from "./Main/GetQuote/GetQuote";
import Products from "./Main/Products/Product";
import Productopen from "./Main/Products/Productopen";
import Dashboard from "./Dashboard/components/dashboard/dashboard";
import Profile from "./Dashboard/components/pages/profile";
import Invoice from "./Dashboard/components/pages/others/invoice";
import Charts from "./Dashboard/components/charts/charts";

export default function App() {
  return (
    <>
      <BrowserRouter>
        <Routes>
          {/*   ===========   User Page   =========== */}
          <Route path="/" element={<Home />} />
          <Route path="/contact" element={<ContactUs />} />
          <Route path="/GetQuote" element={<GetQuote />} />
          <Route path="/products" element={<Products />} />

          {/*   ===========   Combine Page   =========== */}
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<SignUp />} />
          <Route path="/Productopen" element={<Productopen />} />

          {/*   ===========   Admin Page   =========== */}
          <Route path="/Dashboard" element={<Dashboard />} />
          <Route path="/profile" component={<Profile />} />
          <Route path="/invoice" component={<Invoice />} />
          <Route path="/charts" component={<Charts />} />
        </Routes>
      </BrowserRouter>
    </>
  );
}
