import React, { useState } from "react";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import { IconButton } from "@mui/material";
import "./SignUp.css";
import { Link, useNavigate } from "react-router-dom";
import InstagramIcon from "@mui/icons-material/Instagram";
import FacebookIcon from "@mui/icons-material/Facebook";
import logo from "../../Assete/images/Logo/Pentagone tape.png";
import ImgSideSignUp from "../../Assete/images/navbar/abc.png";
import Navbar from "../../NavFoot/NavBar/Navbar";
import Footer from "../../NavFoot/Footer/Footer";
import axios from "axios";
import { serverURL } from "../../configs/Variables";

const SignUp = () => {
  let [name, setName] = useState("");
  let [email, setEmail] = useState("");
  let [phoneNumber, setPhoneNumber] = useState("");
  let [country, setCountry] = useState("");
  let [password, setPassword] = useState("");
  let [signingup, setSigningup] = useState(false);
  let [signupSuccessfull, setSignupSuccessfull] = useState(false);
  let navigate = useNavigate();
  let handleSignup = (e) => {
    e.preventDefault();
    let forServer = { name, email, phoneNumber, country, password };
    setSigningup(true);
    axios
      .post(`${serverURL}/api/user/signup`, forServer)
      .then((response) => {
        setSigningup(false);
        let authToken = response.data.authToken;
        localStorage.setItem("pentagon_packaging", authToken);
        setName("");
        setEmail("");
        setPassword("");
        setCountry("");
        setPhoneNumber("");
        setSignupSuccessfull(true);
        navigate("/login");
      })
      .catch((error) => {
        setSigningup(false);
        console.log(error);
      });
  };
  return (
    <>
      <Navbar />
      <section style={{ marginTop: "50px" }} className="h-100 gradient-form">
        <div className="container py-5 h-100">
          <div className="row d-flex justify-content-center align-items-center h-100">
            <div className="col-xl-10">
              <div className="card rounded-3 text-black">
                <div
                  className="row g-0"
                  style={{ height: "fit-content", transition: "height 0.5s" }}
                >
                  <div className="col-lg-6">
                    <div className="card-body p-md-5 mx-md-4">
                      <div className="text-center">
                        <img
                          src={logo}
                          style={{
                            height: "130px",
                            width: "130px",
                            marginBottom: "30px",
                          }}
                          alt="logo"
                        />
                        {/* <h4 className="mt-1 mb-5 pb-1">Pentagone Packaging</h4> */}
                      </div>
                      {!signupSuccessfull && (
                        <form onSubmit={handleSignup}>
                          <center>
                            <p>Sign Up</p>
                          </center>
                          <div className="form-outline mb-5">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Name"
                              autoComplete="name"
                              value={name}
                              required
                              onChange={(e) => {
                                setName(e.target.value);
                              }}
                            />
                          </div>
                          <div className="form-outline mb-5">
                            <input
                              type="email"
                              className="form-control"
                              placeholder="Email"
                              autoComplete="email"
                              required
                              value={email}
                              onChange={(e) => {
                                setEmail(e.target.value);
                              }}
                            />
                          </div>
                          <div className="form-outline mb-5">
                            <input
                              type="number"
                              className="form-control"
                              placeholder="Phone Number"
                              autoComplete="tel"
                              required
                              value={phoneNumber}
                              onChange={(e) => {
                                setPhoneNumber(e.target.value);
                              }}
                            />
                          </div>
                          <div className="form-outline mb-5">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Country"
                              autoComplete="country"
                              required
                              value={country}
                              onChange={(e) => {
                                setCountry(e.target.value);
                              }}
                            />
                          </div>

                          <div className="form-outline mb-3">
                            <input
                              type="password"
                              className="form-control"
                              placeholder="Password"
                              autoComplete="password"
                              required
                              value={password}
                              onChange={(e) => {
                                setPassword(e.target.value);
                              }}
                            />
                          </div>
                          <div>
                            <a className="text-muted ">Forgot password?</a>
                          </div>
                          <div className="text-center  ">
                            <button
                              className="btn btn-primary btn-block fa-lg gradient-custom-2 borderRadius"
                              type="submit"
                              style={{
                                margin: "30px",
                                backgroundColor: "skyblue",
                              }}
                            >
                              {!signingup && <>Sign Up</>}
                              {signingup && (
                                <>
                                  Signing Up
                                  <i className="fa-solid fa-spinner fa-spin" />
                                </>
                              )}
                            </button>
                          </div>
                          <div className="d-flex justify-content-center pb-4">
                            <p className="mb-0 me-2">Already Have a account?</p>
                            <Link to="/login">
                              <p
                                style={{ color: "skyblue", cursor: "pointer" }}
                              >
                                Log in
                              </p>
                            </Link>
                          </div>
                        </form>
                      )}
                      {signupSuccessfull && (
                        <div
                          style={{
                            fontSize: "2rem",
                            backgroundColor: "green",
                            color: "white",
                            padding: "2rem",
                            borderRadius: "5px",
                          }}
                        >
                          Sign Up Successfull{" "}
                          <i className="fa-solid fa-square-check fa-bounce" />{" "}
                          Plz Login to Continue.{" "}
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="col-lg-6 d-flex align-items-center">
                    <div className="text-white px-3 py-4 p-md-5 mx-md-4">
                      <img
                        src={ImgSideSignUp}
                        style={{ height: "auto", width: "auto" }}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default SignUp;
